.tbl_complexinfo{float:left;width:234px;padding:20px 11px 40px 20px}
.tbl_complexinfo > strong{color:#333}
.tbl_complexinfo table{width:100%;margin-top:9px;border-collapse:collapse;border-top:1px solid #9fa2a9;table-layout:fixed}
.tbl_complexinfo th,.tbl_complexinfo td{padding:4px 0 3px 8px;line-height:19px}
.tbl_complexinfo tr:first-child th,.tbl_complexinfo tr:first-child td{padding-top:9px}
.tbl_complexinfo th{background-color:#fafafa;font-weight:normal;color:#757575;text-align:left;vertical-align:top}
.tbl_complexinfo td{color:#333}
.tbl_detailinfo{float:right;width:433px;padding:20px 20px 40px}
.tbl_detailinfo > strong{color:#333}
.tbl_detailinfo .desc_deatilinfo{position:absolute;top:21px;right:18px;font-size:11px;color:#888}
.tbl_detailinfo table{width:100%;margin-top:9px;border-collapse:collapse;border-top:1px solid #9fa2a9;table-layout:fixed}
.tbl_detailinfo th,.tbl_detailinfo td{padding:12px 0 8px}
.tbl_detailinfo th{background-color:#fafafa;font-size:11px;font-weight:normal;color:#757575}
.tbl_detailinfo td{padding:10px 0;border-top:1px solid #e1e1e1;color:#333;text-align:center}
.tbl_detailinfo td.name_detail{padding-left:8px;text-align:left}
.tbl_detailinfo td.name_detail a{display:inline-block;color:#3d79cc;text-decoration:underline}
.tbl_detailinfo td.name_detail .txt{display:block;margin-top:10px}
.tbl_detailinfo td .thmb_area{float:left;position:relative;margin-right:6px}
.tbl_detailinfo td .thmb_area .thmb_num{position:absolute;right:0;bottom:0;width:16px;height:18px;background:url(#{$imgUrl-service}/search/bg_slide.gif) no-repeat}
.tbl_detailinfo td.expand{padding:14px 0 40px;color:#555}
.tbl_detailinfo td.price{padding-right:22px;text-align:right}
.tbl_detailinfo td .state_agency{display:block;margin-top:10px;color:#818181;text-decoration:underline}
.tbl_detailinfo td.contact_info{color:#333;text-align:left}
.tbl_detailinfo td.contact_info .state_agency{color:#333;text-decoration:none}
.tbl_detailinfo.v2{width:auto}
.tbl_detailinfo.v2 td.name_detail{padding-left:15px}
.section_interior .more a{display:inline-block;position:absolute;right:19px;bottom:20px;padding:0 9px 0 0;background:url(#{$imgUrl-service}/search/sprite_common.gif) no-repeat 100% -39px;color:#0033AC;letter-spacing:-1px;text-decoration:none}
:root .section_interior .more a{background-position:100% -37px}
.tbl_interior_area .more{position:absolute;right:0;bottom:18px;padding-top:9px;border-top:1px solid #e1e1e1;text-align:right}
.tbl_interior_area .tbl_complexinfo .more{right:474px}
.tbl_interior_area .tbl_complexinfo .more{width:235px;margin-right:10px}
.tbl_interior_area .tbl_detailinfo .more{width:435px;margin-right:19px}
.tbl_interior_area .tbl_detailinfo.v2 .more{width:700px;margin-right:19px}
.tbl_interior_area .more a{position:static}

.ad_area_srch{margin:0 0 24px 0}
@media screen and (-webkit-min-device-pixel-ratio:0) {
	.section_option .opt_area .btn_radio {top:2px}
}